div,input,textarea,button{box-sizing:border-box}body{height:100vh;font-family:helvetica;margin:0;padding:0}.faq{max-width:960px;width:100%;margin:0 auto}.faqList{list-style-type:none;padding-left:0}.faqList__element{margin:10px 0;border:1px solid #a0a0a0;border-radius:5px}.faqList__elementContent--question{background-color:cyan;padding:10px;border-bottom:1px solid #a0a0a0;color:blue}.faqList__elementContent--answer{overflow:hidden;height:0}.faqList__elementContent--tick{float:right;padding:0 10px;cursor:pointer}.show{padding:10px;height:auto}
